body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-color: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#root {
  width: 500px;
  min-height: 200px;
  padding: 20px 0;
  border: 1px solid lightgrey;
  border-radius: 10px;
  font-size: 18px;
  position: relative;
}

form {
  padding: 5px 20px;
}

form:focus-within {
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
}

.tasksForm {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.tasksForm:focus-within .fa-times {
  display: block;
}

.tasksForm:hover .fa-times {
  display: block;
}

input[type='text'] {
  border: 0;
  outline: none;
  padding-left: 10px;
  width: 400px;
  background-color: whitesmoke;
  font-size: 18px;
}

input[type='text']::-webkit-input-placeholder {
  font-size: 15px;
}

input[type='text']::-ms-input-placeholder {
  font-size: 15px;
}

input[type='text']::placeholder {
  font-size: 15px;
}

.fas,
.far {
  border: 0;
  outline: none;
  background-color: whitesmoke;
  color: grey;
  cursor: pointer;
}

.fa-times {
  display: none;
}

.fa-plus {
  padding: 0 5px 0 2px;
  font-size: 12px;
}

.fa-square:hover,
.fa-check-square:hover {
  color: black;
}

.menu {
  position: absolute;
  bottom: 10px;
  width: 100%;
}

